@import url(https://fonts.googleapis.com/css2?family=Big+Shoulders+Display:wght@700&family=Lexend+Deca&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body,body div,span,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,abbr,address,cite,code,del,dfn,em,img,ins,kbd,q,samp,small,strong,sub,sup,var,b,i,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,figure,footer,header,hgroup,menu,nav,section,time,mark,audio,video{margin:0;padding:0;border:0;outline:0;font-size:100%;vertical-align:baseline;background:transparent}html{--light-grey-color: #F2F2F2;--white-color: #F2F2F2;--font-family-lexend-deca: Lexend Deca, sans-serif;--font-family-big-shoulders-display: Big Shoulders Display, sans-serif}body{background:#F2F2F2;background:var(--light-grey-color)}body .attribution{visibility:hidden;display:flex;justify-content:center}

.CardContainer{max-width:1440px;width:100%;margin:auto}.CardContainer .row{display:flex;flex-direction:column;width:327px;margin:88px auto 24px}@media screen and (min-width: 376px){.CardContainer .row{flex-direction:row;height:500px;width:920px;margin:171px auto 129px}}@media (max-width: 900px) and (min-width: 375px){.CardContainer .row{flex-direction:column;height:auto;width:auto;margin:0 auto;padding:25px}}.CardContainer .row .col{box-sizing:border-box;position:relative;padding:48px;height:442px}@media screen and (min-width: 376px){.CardContainer .row .col{height:auto}}@media (max-width: 900px) and (min-width: 375px){.CardContainer .row .col{height:442px}}.CardContainer .row .col:first-child{border-radius:8px 8px 0 0}@media screen and (min-width: 376px){.CardContainer .row .col:first-child{border-radius:8px 0 0 8px}}@media (max-width: 900px) and (min-width: 375px){.CardContainer .row .col:first-child{border-radius:8px 8px 0 0}}.CardContainer .row .col:last-child{border-radius:0 0 8px 8px}@media screen and (min-width: 376px){.CardContainer .row .col:last-child{border-radius:0 8px 8px 0}}@media (max-width: 900px) and (min-width: 375px){.CardContainer .row .col:last-child{border-radius:0 0 8px 8px}}.CardContainer .row .col img{margin-bottom:35px}.CardContainer .row .col h2{font-family:var(--font-family-big-shoulders-display);font-style:normal;font-weight:bold;font-size:40px;line-height:48px;text-transform:uppercase;color:var(--light-grey-color);margin-bottom:25px}.CardContainer .row .col p.text{font-family:var(--font-family-lexend-deca);font-style:normal;font-weight:normal;font-size:15px;line-height:25px;color:var(--white-color);mix-blend-mode:normal;opacity:0.75}.CardContainer .row .col a.action.primary{text-decoration:unset;position:absolute;bottom:48px;width:146px;height:48px;background:var(--white-color);border-radius:25px;display:flex;justify-content:center;align-items:center;transition:all .3s ease}.CardContainer .row .col a.action.primary:hover{background:transparent;border:2px solid var(--white-color)}.CardContainer .row .col a.action.primary:hover>span{color:var(--white-color) !important}.CardContainer .row .col a.action.primary>span{text-transform:capitalize;font-family:var(--font-family-lexend-deca);font-style:normal;font-weight:normal;font-size:15px;line-height:25px}

